@import '../base';

.button-color-scheme {
  @include flex-between;
  position: relative;
  padding: 2px;
  width: 40px;
  border-radius: 11px;
  background-color: #b2b8c5;
  color: white;
  transition: 200ms;

  &-active {
    background-color: $color-active;
  }

  &:hover {
    cursor: pointer;
  }

  .toggle-head {
    @include flex-center;
    padding: 0;
    width: 18px;
    height: 18px;
    border-radius: 100%;
    box-shadow: 1px 1px 2px #86898d;
    background-color: #e7e7e7;
    color: #333333;
    font-size: 10px;
    transition: 200ms;

    &-active {
      background-color: #333333;
      color: #e7e7e7;
    }
  }  
}