@import '../base';

.about {
  @extend .section;

  &-profile {
    margin-right: 100px;
    
    &-name-kr {
      margin: 0;
      font-size: 3rem;
    }
  
    &-name-en {
      margin: 0;
      font-size: 2rem;
      color: $color-inactive;
    }
  }

  p {
    font-size: 2.5rem;
  }
}

.test {
  background: grey;
  h2 { color: red }

  &-wrap {
    border: 1px solid black;
  }
}